/**
 * Factory des widgets d'affichage des critÃ¨res de recherche.
 * Par dÃ©faut, la liste des critÃ¨res disponibles est issue de la liste d'extPoints :
 * - "wsp.crit.items" pour les recherches d'items.
 * - "wsp.crit.tasks" pour les recherches de tÃ¢ches.
 */
export interface IUiCritArea extends IArea<OUiCritInit, IUiCritWidget> {

	/** Signature du critÃ¨re pour gÃ©rer les alternatives autorisÃ©es. */
	critSgn?: string

	/** Evalue si un critÃ¨re issu de cette factory pourrait remplacer le critÃ¨re instanciÃ© passÃ© en paramÃ¨tre. */
	canReplace(uiCrit: IUiCritWidget, ctx: OUiCritInit): boolean

	/** Construit un rÃ©sumÃ© HTML textuel (enrichi d'Ã©ventuels tooltips) de ce critÃ¨re de recherche. */
	buildAbstract(xml: Element, ctx: OUiCritInit, options?: any): HTMLElement | undefined

	/** Construit des critÃ¨re de recherches Wed internes Ã  l'item pour poursuivre la requete dans l'item. */
	buildWedSearch?(xml: Element, criterions: Dict<IUiCritArea>): JWedSearchBarParams | undefined
}

/**
 *
 */
export interface IUiCritWidget extends BaseElement {

	critArea: IUiCritArea

	initialize(init: OUiCritInit): this;

	/** Initialisation du critÃ¨re Ã  partir de sa forme xml. Doit Ãªtre appelÃ© aprÃ¨s initialize(init). */
	initFromXmlCrit(xml: Element): Promise<this>

	/**
	 * Initialise ce critÃ¨re lorsqu'il est issu d'une action d'insertion / remplacement de l'utilisateur.
	 * Permet d'enclencher l'ouverture auto d'un popup par ex.
	 * La mÃ©thode this.dispatchChangeEvent() est automatiquement appelÃ©e par cette mÃ©thode.
	 * Cette mÃ©thode doit Ãªtre appelÃ© aprÃ¨s initialize(init).
	 */
	initFromInsAction(xml?: Element): Promise<this>

	buildCrit(): ISearchCrit | undefined

	buildUiXmlCrit(): Element | undefined

	/** Dispatch un event 'wsp-uicrit-change' signalant un changement. */
	dispatchChangeEvent(): void
}

export function IS_UiCritWidget(n: any): n is IUiCritWidget {return n instanceof HTMLElement && ('buildCrit' in n)}

/**
 * Recherche d'un constructeur de widget de critÃ¨re Ã  partir de sa reprÃ©sentation xml.
 */
export function buildCritAreaFromXml(elt: Element, criterions: Dict<IUiCritArea>): IUiCritArea | undefined {
	if (!elt) return undefined;
	const key = elt.getAttribute('uiCrit') || elt.localName;
	return (criterions ? criterions[key] : null) || UiCritUnknown.AREA;
}


/** Impl de base de IUiCritArea */
export class UiCritArea extends SimpleTagArea<OUiCritInit, IUiCritWidget> implements IUiCritArea {

	/** Pattern de regroupement des critÃ¨res remplaÃ§ables entre eux. */
	replacePattern?: RegExp;

	critSgn?: string;

	buildBody(ctx: OUiCritInit): IUiCritWidget {
		const w = super.buildBody(ctx);
		w.critArea = this;
		return w;
	}

	canReplace(uiCrit: IUiCritWidget, ctx: OUiCritInit): boolean {
		if (!this.replacePattern || !uiCrit.critArea.critSgn) return false;
		return this.replacePattern.test(uiCrit.critArea.critSgn);
	}

	setCritSgn(critSgn: string): this {
		this.critSgn = critSgn;
		return this;
	}

	setReplacePattern(replacePattern: RegExp): this {
		this.replacePattern = replacePattern;
		return this;
	}

	setCanReplace(fct: (uiCrit: IUiCritWidget, ctx: OUiCritInit) => boolean): this {
		this.canReplace = fct;
		return this;
	}

	buildAbstract(xml: Element, ctx: OUiCritInit, options?: any): HTMLElement | undefined {
		return <span class="crit">{this.getLabel(ctx)}</span>;
	}

	setBuildAbstract(fct: (xml: Element, ctx: OUiCritInit, options?: any) => HTMLElement | undefined): this {
		this.buildAbstract = fct;
		return this;
	}

	buildWedSearch(xml: Element, criterions: Dict<IUiCritArea>): JWedSearchBarParams | undefined {return undefined}

	setBuildWedSearch(fct: (xml: Element, criterions: Dict<IUiCritArea>) => JWedSearchBarParams): this {
		this.buildWedSearch = fct;
		return this;
	}
}

export interface OUiCritInit extends OSkinableInit {
	reg: IReg<IWspUiEnv>

	/**
	 * Map des critÃ¨res disponibles pour ce contexte de recherche. La key de la map est l'id de l'Area
	 * qui doit Ãªtre la valeur de l'attribut "uiCrit" de la forme sÃ©rialisÃ©e en XML du critÃ¨re.
	 * Par dÃ©faut, la liste des critÃ¨res disponibles est issue de la liste d'extPoints :
	 * - "wsp.crit.items" pour les recherches d'items.
	 * - "wsp.crit.tasks" pour les recherches de tÃ¢ches.
	 */
	criterions?: Dict<IUiCritArea>;

	hideAlternativesBtn?: boolean
}

export interface OUiCritWithInputInit {
	placeholder?: string
}

/**
 * Widget de base pour l'affichage d'un critÃ¨re de recherche.
 * Doit Ã©mettre un event "wsp-uicrit-change" Ã  chaque changement effectif du critÃ¨re.
 */
export abstract class UiCritBase extends BaseElement implements IUiCritWidget {

	/** Factory de ce widget*/
	critArea: IUiCritArea;

	reg: IReg<IWspUiEnv>;

	criterions: Dict<IUiCritArea>;

	/** this.dispatchChangeEvent.bind(this) */
	protected onChange: () => void;

	protected _initialize(init: OUiCritInit): void {
		if (!this.criterions) this.criterions = init.criterions;
		this.reg = this.findReg(init);
		this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin('wsp-uicrit', this.shadowRoot);
		this._initAndInstallSkin(this.localName, init);
		this.onChange = this.dispatchChangeEvent.bind(this);
	}

	static initHeadFromArea(critArea: IUiCritArea, ctx: OUiCritInit, headElt: HTMLElement) {
		if (critArea && headElt) {
			const desc = critArea.getDescription(ctx);
			if (desc) headElt.title = desc;
		}
	}

	buildInitFromAtts(init?: OUiCritInit): any {
		init = super.buildInitFromAtts(init);
		if (!init.criterions) {
			const criterionsHolder = DOMSH.findParentElt(this, null, (n: any): n is Element => (n as OUiCritInit).criterions != null) as any as OUiCritInit;
			if (criterionsHolder) init.criterions = criterionsHolder.criterions;
		}
		return init;
	}

	abstract initFromXmlCrit(xml: Element): Promise<this>;

	async initFromInsAction(xml?: Element): Promise<this> {
		if (xml) await this.initFromXmlCrit(xml);
		this.dispatchChangeEvent();
		return this;
	}

	abstract buildCrit(): ISearchCrit | undefined;

	buildUiXmlCrit(): Element | undefined {
		const crit = this.buildCrit();
		if (!crit) return undefined;
		const elt = crit.toDom();
		if (elt) elt.setAttribute("uiCrit", this.getUiCritXmlAtt());
		return elt;
	}

	/** Valeur de l'attribut XML 'uiCrit' utilisÃ© pour sÃ©rialiser ce critÃ¨re en XML. */
	protected getUiCritXmlAtt(): string {
		return this.localName;
	}

	dispatchChangeEvent() {
		this.dispatchEvent(new CustomEvent('wsp-uicrit-change', {bubbles: true}));
	}

	protected insertAlternativesBtn(init: OUiCritInit): ButtonActions<OUiCritInit> | null {
		if (init.hideAlternativesBtn) return null;

		class CritAction extends Action<OUiCritInit> {
			constructor(public area: IUiCritArea, public replace: UiCritBase) {
				super(area.getId());
			}

			getLabel(ctx: OUiCritInit): string {return this.area.getLabel(ctx)}

			async execute(ctx: OUiCritInit, ev?: Event) {
				const elt = (await this.area.loadBody(ctx)).initialize(ctx);
				const currentCritXml = this.replace.buildUiXmlCrit();
				this.replace.parentNode.insertBefore(elt, this.replace);
				this.replace.remove();
				elt.initFromInsAction(currentCritXml);
			}
		}

		const actions = [] as IAction<OUiCritInit>[];
		for (const area of Object.values(init.criterions)) {
			if (area !== this.critArea && area.canReplace(this, init) && area.isVisible(this)) actions.push(new CritAction(area, this));
		}
		return actions.length > 0 ? this.buildAlternativesBtn(actions, this.findReg(init)) : null;
	}

	protected buildAlternativesBtn(actions: IAction<OUiCritInit>[], reg: IReg<IWspUiEnv>): ButtonActions<OUiCritInit> | null {
		return <ButtonActions class="altBtn" Ã®={{
			reg: reg,
			label: "â",
			actions: actions,
			actionContext: this
		}}/> as ButtonActions<OUiCritInit>;
	}

}

