/** Area pour les opÃ©rateurs boolÃ©en AND / OR intÃ©grant le sous opÃ©rateur NOT optionnel pour chaque fils. */
class UiCritBoolArea extends Area<OUiCritInit, IUiCritWidget> implements IUiCritArea {

	constructor(id: 'and' | 'or', public hideCritHeader?: boolean) {
		super(id);
		if (!hideCritHeader) this._group = "bool";
	}

	getLabel(ctx: OUiCritInit): string {return this._id === 'or' ? "Groupe de critÃ¨res (... OU ...)" : "Groupe de critÃ¨res (... ET ...)"}

	buildBody(ctx: OUiCritInit): IUiCritWidget {
		const w = new UiCritBool();
		w.critArea = this;
		return w;
	}

	canReplace(uiCrit: IUiCritWidget, ctx: OUiCritInit): boolean {return uiCrit instanceof UiCritBool}

	buildAbstract(xml: Element, ctx: OUiCritInit, options?: any): HTMLElement | undefined {
		const liaise = this._id === 'or' ? " OU " : " ET ";
		const liaiseNot = this._id === 'or' ? " OU SAUF " : " ET SAUF ";
		const chAbstract = [] as HTMLElement[];
		for (let ch = xml.firstElementChild; ch; ch = ch.nextElementSibling) {
			const isNot = ch.localName === 'not';
			const chXml = isNot ? ch.firstElementChild : ch;
			const chCrit = buildCritAreaFromXml(chXml, ctx.criterions);
			const sub = chCrit ? chCrit.buildAbstract(chXml, ctx) : <span class="critUnknown"><span class="xml">{DOM.ser(chXml)}</span></span>;
			if (sub) {
				if (chAbstract.length > 0) chAbstract.push(<span class="boolOp">{isNot ? liaiseNot : liaise}</span>);
				else if (isNot) chAbstract.push(<span class="boolOp"> SAUF </span>);
				chAbstract.push(sub);
			}
		}
		if (chAbstract.length === 0) return undefined;
		if (this.hideCritHeader) return <span>{chAbstract}</span>;
		return <span class="boolGroup">
			<span class="boolBracket">(</span>
			{chAbstract}
			<span class="boolBracket">)</span>
			</span>;
	}

	buildWedSearch(xml: Element, criterions: Dict<IUiCritArea>): JWedSearchBarParams {
		for (let ch = xml.firstElementChild; ch; ch = ch.nextElementSibling) {
			const chCrit = buildCritAreaFromXml(ch, criterions);
			if (chCrit && ("buildWedSearch" in chCrit)) {
				const s = chCrit.buildWedSearch(ch, criterions);
				if (s) return s;
			}
		}
	}
}

/**
 * Widgets pour les opÃ©rateurs boolÃ©ens AND et OR.
 */
export class UiCritBool extends UiCritBase {

	static AREA_AND = new UiCritBoolArea('and');
	static AREA_OR = new UiCritBoolArea('or');
	static AREA_AND_ROOT = new UiCritBoolArea('and', true);
	static AREA_OR_ROOT = new UiCritBoolArea('or', true);

	critArea: UiCritBoolArea;

	get booleanOp(): 'and' | 'or' {return this.critArea.getId() as 'and' | 'or'};

	label: HTMLElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		if (!this.critArea.hideCritHeader) {
			const head = this.shadowRoot.appendChild(<div id="critHead"/>);
			this.label = head.appendChild(<span/>);
			head.appendChild(this.buildAlternativesBtn([new Action<OUiCritInit>('altBool')
				.setLabel((ctx: OUiCritInit) => {
					return this.critArea === UiCritBool.AREA_AND ? UiCritBool.AREA_OR.getLabel(ctx) : UiCritBool.AREA_AND.getLabel(ctx);
				})
				.setExecute(() => {
					this.critArea = this.critArea === UiCritBool.AREA_AND ? UiCritBool.AREA_OR : UiCritBool.AREA_AND;
					this._refresh();
				})], this.reg));
		}
		this.shadowRoot.appendChild(<slot id="critCh"/>);
		this.addEventListener("wsp-uicrit-toDelete", (ev: CustomEvent) => {
			const ch = ev.target as Node;
			if (ch && ch.parentElement === this) this.deleteCrit(ch as IUiCritWidget);
			ev.stopImmediatePropagation();
		})
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this.refreshFreeze(1);
		try {
			for (let ch = xml.firstElementChild; ch; ch = ch.nextElementSibling) {
				const isNot = ch.localName === 'not';
				const critXml = isNot ? ch.firstElementChild : ch;
				const critArea = buildCritAreaFromXml(critXml, this.criterions);
				if (critArea) {
					const crit = await critArea.loadBody(this);
					if (crit) {
						crit.initialize(this);
						await crit.initFromXmlCrit(critXml);
						this.insertCrit(crit, isNot);
					}
				}
			}
		} finally {
			this.refreshFreeze(-1);
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const crit = this.booleanOp === 'or' ? new SearchOr() : new SearchAnd();
		for (let ch = DOM.findFirstChild(this, IS_UiCritWidget); ch; ch = DOM.findNextSibling(ch, IS_UiCritWidget)) {
			LANG.pushIfDefined(crit, ch.buildCrit());
		}
		return crit.length > 0 ? crit : undefined;
	}

	buildUiXmlCrit(): Element | undefined {
		return JSX.asXml(() => {
			const root = this.booleanOp === 'or' ? <or/> : <and/>;
			if (!this.critArea.hideCritHeader) root.setAttribute("uiCrit", this.booleanOp === 'or' ? "wsp-uicrit-bool:or" : "wsp-uicrit-bool:and");
			for (let ch = DOM.findFirstChild(this, IS_UiCritWidget); ch; ch = DOM.findNextSibling(ch, IS_UiCritWidget)) {
				const sub = ch.buildUiXmlCrit();
				if (sub) root.appendChild(sub);
			}
			return root; //NON root.hasChildNodes() ? root : undefined; Sinon mauvaise reconstruction graphique.
		});
	}

	insertCrit(crit: IUiCritWidget, notOp?: boolean, insertBefore?: HTMLElement) {
		const notOpt = new UiCritNotOptional().initialize(this).initFromContext(this.booleanOp, notOp);
		//TODO notOpt.setAttribute("draggable", "true");
		notOpt.appendChild(crit);
		this.insertBefore(notOpt, insertBefore);
		crit.initFromInsAction();
		this.refresh();
	}

	deleteCrit(crit: IUiCritWidget) {
		crit.remove();
		this.dispatchChangeEvent();
		this.refresh();
	}

	_refresh() {
		const op = this.booleanOp;
		if (this.label) DOM.setTextContent(this.label, op === 'and' ? "Tous les sous-critÃ¨res sont remplis" : "Au moins un des sous-critÃ¨res est rempli");
		let insFound = false;
		for (let ch = this.firstElementChild; ch;) {
			const next = ch.nextElementSibling;
			if (ch instanceof Button) {
				if (insFound) ch.remove();
				else insFound = true;
			} else if (ch instanceof UiCritNotOptional) {
				ch.booleanContext = op;
				if (!insFound) this._insertAddBtn(ch);
				else insFound = false;
			}
			ch = next;
		}
		if (!insFound) this._insertAddBtn(null);
	}

	protected _insertAddBtn(before: Element) {
		this.insertBefore(<Button class="insCrit" skin="wsp-uicrit-bool/insCrit" label="+" title="InsÃ©rer un critÃ¨re" onclick={this.onInsert}/>, before);
	}

	protected onInsert(this: Button, ev: MouseEvent) {

		class CritAction extends Action<Button> {
			constructor(public critArea: IUiCritArea) {
				super(critArea.getId());
			}

			getGroup(ctx: Button): string {
				return this.critArea.getGroup(ctx.parentElement as UiCritBool);
			}

			getLabel(ctx: Button): string {
				return this.critArea.getLabel(ctx.parentElement as UiCritBool);
			}

			getIcon(ctx: Button): string {
				return this.critArea.getIcon(ctx.parentElement as UiCritBool);
			}

			getDescription(ctx: Button): string {
				return this.critArea.getDescription(ctx.parentElement as UiCritBool);
			}

			isVisible(ctx: Button): boolean {return this.critArea.isVisible(ctx.parentElement as UiCritBool)}

			isEnabled(ctx: Button): boolean {return this.critArea.isEnabled(ctx.parentElement as UiCritBool)}

			async execute(ctx: Button, ev?: Event) {
				const critCtn = ctx.parentElement as UiCritBool;
				critCtn.insertCrit(await this.critArea.loadBody(critCtn), false, ctx);
			}
		}

		const critCtn = this.parentElement as UiCritBool;
		let crits = Object.values(critCtn.criterions)
			.filter((area) => area.getGroup(critCtn))
			.map((area) => new CritAction(area)) as IAction<Button>[];
		crits = ACTION.injectSepByGroup(crits, "props bool scope * advanced", this);

		POPUP.showPopupActionsFromEvent({
			actions: crits,
			actionContext: this
		}, ev);
	}

}


