/**
 * Container d'un et d'un seul 'vrai' IUiCritWidget destinÃ© Ã  gÃ©rer le critÃ¨re optionnel not() pour chaque
 * fils d'un IUiCritWidget AND ou OR.
 */
export class UiCritNotOptional extends UiCritBase {

	public popupBtn: ButtonActions<UiCritNotOptional>;

	protected _booleanContext: 'and' | 'or';
	protected _isNot: boolean;

	get booleanContext(): 'and' | 'or' {return this._booleanContext;}

	set booleanContext(v: 'and' | 'or') {
		if (this._booleanContext !== v) {
			this._booleanContext = v;
			this._refresh();
		}
	}

	get isNot(): boolean {return this._isNot}

	set isNot(v: boolean) {
		if (this._isNot !== v) {
			this._isNot = v;
			this._refresh();
			this.dispatchChangeEvent();
		}
	}

	getChildCrit(): IUiCritWidget | null {return DOM.findFirstChild(this, IS_UiCritWidget)}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);

		class NotAction extends Action<UiCritNotOptional> implements IActionToggle<UiCritNotOptional> {
			constructor(public isNot: boolean, public crit: UiCritNotOptional) {
				super(isNot ? 'not' : '');
				if (isNot) {
					this._label = function (ctx: UiCritNotOptional) {return ctx.booleanContext === 'or' ? "OU SAUF" : "ET SAUF"};
				} else {
					this._label = function (ctx: UiCritNotOptional) {return ctx.booleanContext === 'or' ? "OU" : "ET"};
				}
			}

			isToggle(ctx: UiCritNotOptional): this is IActionToggle<UiCritNotOptional> {return true}

			/** null pour l'Ã©tat 'mixed'. */
			getDatas(api: 'toggle', ctx: UiCritNotOptional): boolean | null {return this.isNot === ctx.isNot}

			isEnabled(ctx: UiCritNotOptional): boolean {
				if (this.isNot === ctx.isNot) return false;
				return super.isEnabled(ctx);
			}

			execute(ctx: UiCritNotOptional, ev?: Event): any | "noPreventDefault" {
				this.crit.isNot = this.isNot;
			}
		}

		this.popupBtn = this.shadowRoot.appendChild(<ButtonActions Ã®={{
			actionContext: this,
			actions: [new NotAction(false, this), new NotAction(true, this)]
		} as OButtonActionsInit<UiCritNotOptional>}/> as ButtonActions<UiCritNotOptional>);
		this.shadowRoot.appendChild(<slot/>);
		this.shadowRoot.appendChild(<Button id="del" title="Supprimer" icon="/@skin@/commons/icons/delete.svg" onclick={this.onDelete}/> as Button);
	}

	initFromContext(booleanContext: 'and' | 'or', isNot: boolean): this {
		this._booleanContext = booleanContext;
		this._isNot = isNot;
		this.refresh();
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const ch = this.getChildCrit();
		if (!ch) return undefined;
		const crit = ch.buildCrit();
		if (!crit) return undefined;
		return this._isNot ? new SearchNot(crit) : crit;
	}

	buildUiXmlCrit(): Element | undefined {
		const ch = this.getChildCrit();
		if (!ch) return undefined;
		const crit = ch.buildUiXmlCrit();
		return crit && this._isNot ? <not>{crit}</not> : crit;
	}

	protected onDelete(this: Button, ev: MouseEvent) {
		DOMSH.findHost(this).dispatchEvent(new CustomEvent("wsp-uicrit-toDelete", {bubbles: true, composed: true}));
	}

	protected _refresh() {
		this.popupBtn.label = this.isNot ? this.booleanContext === 'or' ? "OU SAUF" : "ET SAUF" : this.booleanContext === 'or' ? "OU" : "ET";
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		//NOT USED.
		return this;
	}
}

