/**
 * UiCritFree
 * @uiLabel : label libre de cette condition exploitÃ© dans l'abstract
 */
export class UiCritFree extends UiCritBase {

	static AREA = new UiCritArea('wsp-uicrit-free').setLabel("Condition XML avancÃ©e").setGroup("advanced").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
		const label = xml.getAttribute("uiLabel");
		return label ? <span class="crit"><span class="critVal">{label}</span></span> : <span class="crit">Condition avancÃ©e</span>;
	});

	input: HTMLTextAreaElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this.reg.installSkin('wsp-uicrit-free', sr);
		sr.appendChild(<div id="critHead">
			<span>Condition XML avancÃ©e</span>
			{this.insertAlternativesBtn(init)}
		</div>);
		this.input = sr.appendChild(<textarea onblur={this.onBlur} oninput={this.onInput}/>) as HTMLTextAreaElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this.input.value = DOM.ser(xml);
		this.validateInput();
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (!this.input.value) return undefined;
		const dom = DOM.parseDomValid(this.input.value);
		if (!dom) return undefined;
		return new SearchFree(dom.documentElement);
	}

	validateInput(): boolean {
		const txt = this.input.value;
		const dom = txt ? DOM.parseDomValid(txt) : null;
		this.input.setCustomValidity(dom ? "" : "Cette condition XML n'est pas syntaxiquement correcte");
		return this.input.reportValidity();
	}

	_dirty: boolean;

	onInput(this: HTMLTextAreaElement) {
		const me = DOMSH.findHost<UiCritFree>(this);
		me._dirty = true;
	}

	onBlur(this: HTMLTextAreaElement) {
		const me = DOMSH.findHost<UiCritFree>(this);
		if (me._dirty) {
			me._dirty = false;
			if (me.validateInput()) me.onChange();
		}
	}

}

