/**
 * UiCritSrcUri
 */
export class UiCritSrcUri extends UiCritBase {

	static AREA = new UiCritArea("wsp-uicrit-srcuri-regexp").setLabel("Chemin de l'item")
		.setCritSgn("string").setReplacePattern(/\bstring\b/).setGroup("advanced").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const searchValue = xml.getAttribute("uiPathSearch") || "";
			const searchTxt = `Chemin de l'item contient "${searchValue}"`;
			return <span class="crit"><span class="critVal">{searchTxt}</span></span>;
		});

	protected _textElt: HTMLInputElement;
	protected _regExpElt: HTMLInputElement;

	get isRegExp(): boolean {return this._regExpElt ? this._regExpElt.checked : false}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this._textElt = sr.appendChild(<div class="critHead">
			<span class="critTitle">Chemin</span>
			<input type="search" oninput={this.onChange}/>
			{this.insertAlternativesBtn(init)}
		</div>).querySelector("input");
		const optsRoot = sr.appendChild(<div class="h"/>);
		this._regExpElt = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>Expression rÃ©guliÃ¨re</label>).firstElementChild as HTMLInputElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		if (xml.hasAttribute('uiPathSearch')) {
			// texte
			this._textElt.value = xml.getAttribute('uiPathSearch') || "";
			this._regExpElt.checked = false;
		} else {
			// regExp
			this._textElt.value = xml.getAttribute('regexp') || "";
			this._regExpElt.checked = true;
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v = this._textElt.value;
		if (v && this.isRegExp) {
			return new SearchRegexpUri(new RegExp(v));
		} else if (v) {
			const regexp = new RegExp(".*" + v.replace(/\W/g, LANG.escapeChar4Regexp) + ".*", "i");
			return new SearchRegexpUri(regexp);
		} else
			return undefined;
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit && !this.isRegExp) crit.setAttribute("uiPathSearch", this._textElt.value);
		return crit;
	}

}

customElements.define("wsp-uicrit-srcuri-regexp", UiCritSrcUri);


/**
 * UiCritSrcUri
 */
export class UiCritXpath extends UiCritBase {

	static AREA = new UiCritArea("wsp-uicrit-xpath").setLabel("XPath")
		.setCritSgn("string").setReplacePattern(/\bstring\b/).setGroup("advanced").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const searchTxt = <span class="critVal">{xml.getAttribute("xpathSearch") || ""}</span>;
			return <span class="crit">XPath : '{searchTxt}'</span>;
		});

	protected _xpathElt: HTMLInputElement;

	// key / value
	protected _Ns: Map<string, string> = new Map<string, string>();

	protected _DefaultNsCodeList: string;

	constructor(public nsListCode: string = "namespaces:defaultList") {
		super();
		this._DefaultNsCodeList = nsListCode;
	}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this._xpathElt = sr.appendChild(<div class="critHead">
			<span class="critTitle">XPath</span>
			<input type="search" oninput={this.onInput} onblur={this.onBlur}/>
			{this.insertAlternativesBtn(init)}
		</div>).querySelector("input");

		// TODO : ajouter un composant UI d'Ã©dition des ns
		// Ajout des NS par dÃ©faut
		const nsList = this.reg.getListAsMap(this._DefaultNsCodeList) as Dict<string>;
		if (nsList) for (const prefix in nsList)
			this._Ns.set(prefix == "#default" ? "" : prefix, nsList[prefix]);

	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this._xpathElt.value = xml.getAttribute('xpathSearch') || "";
		let ch = xml.firstElementChild;
		while (ch) {
			if (ch.localName == "ns")
				this._Ns.set(ch.getAttribute("prefix"), ch.getAttribute("uri"));
			ch = ch.nextElementSibling;
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v = this._xpathElt.value;
		if (v)
			return new SearchFullXPath(v, this._Ns);
		return undefined;
	}

	_dirty: boolean;

	onInput(this: HTMLTextAreaElement) {
		const me = DOMSH.findHost<UiCritXpath>(this);
		me._dirty = true;
	}

	onBlur(this: HTMLTextAreaElement) {
		const me = DOMSH.findHost<UiCritXpath>(this);
		if (me._dirty) {
			me._dirty = false;
			me.onChange();
		}
	}

}

customElements.define("wsp-uicrit-xpath", UiCritXpath);

/**
 *  UiCritSubRequest
 */

/** Area pour les opÃ©rateurs boolÃ©en AND / OR intÃ©grant le sous opÃ©rateur NOT optionnel pour chaque fils. */
class UiCritSubRequestArea extends UiCritArea implements IUiCritArea {

	constructor(id: 'desc' | 'asc' | 'parents' | 'children', public traitsCodeList: string | null = "linktraits:search:defaultList") {
		super(id);
	}

	getLabel(ctx: OUiCritInit): string {
		switch (this._id) {
		case "desc" :
			return "Sous-requÃªte dans le rÃ©seau descendant de l'item courant";
		case "asc" :
			return "Sous-requÃªte dans le rÃ©seau ascendant de l'item courant";
		case "children" :
			return "Sous-requÃªte dans les items liÃ©s de l'item courant";
		default :
			return "Sous-requÃªte dans les items liant l'item courant";
		}
	}

	getGroup(ctx: OUiCritInit): string {
		return "advanced";
	}

	buildBody(ctx: OUiCritInit): IUiCritWidget {
		const w = new UiCritSubRequest();
		w.critArea = this;
		return w;
	}

	canReplace(uiCrit: IUiCritWidget, ctx: OUiCritInit): boolean {return uiCrit instanceof UiCritSubRequest}

	buildAbstract(xml: Element, ctx: OUiCritInit, options?: any): HTMLElement | undefined {
		const operator = <span class="critVal">{UiCritSubRequest.OP[xml.getAttribute("op")][1]}</span>;
		const value = <span class="critVal">{xml.getAttribute("value")}</span>;

		const LinkExpXml = UiCritSubRequestArea.findSubExpLinksElement(xml);
		const subCritRootXml = LinkExpXml?.nextElementSibling;

		const subCritRoot = subCritRootXml ? buildCritAreaFromXml(subCritRootXml, ctx.criterions) : null;
		const subCritRootAbstract = subCritRoot?.buildAbstract(subCritRootXml, ctx);
		const linkTraitFilter = LinkExpXml?.getAttribute('linkTraitFilter') || "";
		let linkTraitFilterAbstract: string = null;
		if (linkTraitFilter) {
			const traits = ctx.reg.getListAsMap(this.traitsCodeList) as Dict<string> || {};
			linkTraitFilterAbstract = `filtre '${traits[linkTraitFilter] || linkTraitFilter}'`;
		}

		let areaShortLabel;
		switch (this._id) {
		case "desc" :
			areaShortLabel = <span>{linkTraitFilterAbstract ? `Nombre d'items descendants (${linkTraitFilterAbstract}) tels que :` : 'Nombre d\'items descendants tels que :'} {subCritRootAbstract} {operator} {value}</span>;
			break;
		case "asc" :
			areaShortLabel = <span>{linkTraitFilterAbstract ? `Nombre d'items ascendants (${linkTraitFilterAbstract}) tels que :` : 'Nombre d\'items ascendants tels que :'} {subCritRootAbstract} {operator} {value}</span>;
			break;
		case "children" :
			areaShortLabel = <span>{linkTraitFilterAbstract ? `Nombre d'items liÃ©s (${linkTraitFilterAbstract}) tels que :` : 'Nombre d\'items liÃ©s tels que :'} {subCritRootAbstract} {operator} {value}</span>;
			break;
		default :
			areaShortLabel = <span>{linkTraitFilterAbstract ? `Nombre d'items liants (${linkTraitFilterAbstract}) tels que :` : 'Nombre d\'items liants tels que :'} {subCritRootAbstract} {operator} {value}</span>;
		}
		return <span class="boolGroup">{areaShortLabel}</span>;
	}

	/** Retourne l'exp "exp[path="."]" dans les 2 contextes possibles :
	 * 	- exp[@type="OccurencesSubExp"]/and/exp[path="."]
	 * 	- OU exp[@type="OccurencesSubExp"]/exp[path="."]*/
	static findSubExpLinksElement(xml: Element): Element | null {
		if (xml?.firstElementChild?.localName === 'and')
			return xml.firstElementChild.firstElementChild?.getAttribute("path") === "." ?
				xml.firstElementChild.firstElementChild :
				null;
		else
			return xml?.firstElementChild?.getAttribute("path") === "." ?
				xml.firstElementChild :
				null;
	}
}


export class UiCritSubRequest extends UiCritBase {

	static AREA_DESC = new UiCritSubRequestArea("desc");

	static AREA_ASC = new UiCritSubRequestArea("asc");

	static AREA_CHILDREN = new UiCritSubRequestArea("children");

	static AREA_PARENTS = new UiCritSubRequestArea("parents");

	static OP: { [index: string]: any } = {
		//"value" : [longLabel, shortLabel]
		"gt": ["Items trouvÃ©s supÃ©rieur Ã ", ">"],
		"gt=": ["Items trouvÃ©s supÃ©rieur ou Ã©gal Ã ", ">="],
		"lt": ["Items trouvÃ©s infÃ©rieur Ã ", "<"],
		"lt=": ["Items trouvÃ©s infÃ©rieur ou Ã©gal Ã ", "<="],
		"=": ["Items trouvÃ©s Ã©gal Ã ", "="],
	};

	critArea: UiCritSubRequestArea;

	_opElt: HTMLSelectElement;

	_opValueElt: HTMLInputElement;

	_subExpElt: UiCritBool;

	_trFilterElt: HTMLSelectElement | null;

	get traitsCodeList(): string | null {return this.critArea.traitsCodeList}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		const head = sr.appendChild(<div id="critHead"/>);
		UiCritBase.initHeadFromArea(this.critArea, {reg: this.reg}, head);
		head.appendChild(<span class="critTitle">{this.critArea.getLabel(null)}</span>);
		const buttonActions = this.insertAlternativesBtn(init);
		if (buttonActions) head.appendChild(buttonActions);

		if (this.traitsCodeList) {
			const traits = this.reg.getListAsMap(this.traitsCodeList) as Dict<string>;
			if (traits) {
				this._trFilterElt = <select onchange={this.onChange}>
					<option value="" selected>Aucun filtre</option>
				</select> as HTMLSelectElement;
				for (const tr in traits)
					this._trFilterElt.appendChild(<option value={tr}>{traits[tr]}</option>);
				sr.appendChild(<div class="h">
					{this._trFilterElt}
				</div>)
			}
		}

		const optsRoot = sr.appendChild(<div class="h"/>);
		this._opElt = optsRoot.appendChild(<select onchange={this.onChange}>
			<option value="gt" selected>{UiCritSubRequest.OP["gt"][0]}</option>
			<option value="gt=">{UiCritSubRequest.OP["gt="][0]}</option>
			<option value="lt">{UiCritSubRequest.OP["lt"][0]}</option>
			<option value="lt=">{UiCritSubRequest.OP["lt="][0]}</option>
			<option value="=">{UiCritSubRequest.OP["="][0]}</option>
		</select>) as HTMLSelectElement;
		this._opValueElt = optsRoot.appendChild(<input value="0" type="number" min="0" step="1" oninput={this.onChange} onchange={this.onChange}/>) as HTMLInputElement;

		sr.appendChild(<slot id="critCh"/>);
		this._subExpElt = this.appendChild(UiCritBool.AREA_AND.buildBody(this)) as UiCritBool;
		this._subExpElt.initialize(this);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const LinkExpXml = UiCritSubRequestArea.findSubExpLinksElement(xml);
		if (xml?.hasAttribute("op")) this._opElt.value = xml.getAttribute("op");
		if (xml?.hasAttribute("value")) this._opValueElt.value = xml.getAttribute("value");
		const linkTraitFilter = LinkExpXml?.getAttribute('linkTraitFilter');
		if (linkTraitFilter && this._trFilterElt)
			this._trFilterElt.value = linkTraitFilter

		if (LinkExpXml?.nextElementSibling)
			await this._subExpElt.initFromXmlCrit(LinkExpXml.nextElementSibling);
		return this;
	}

	async initFromInsAction(xml?: Element): Promise<this> {
		const LinkExpXml = UiCritSubRequestArea.findSubExpLinksElement(xml);
		const linkTraitFilter = LinkExpXml?.getAttribute('linkTraitFilter');
		if (linkTraitFilter && this._trFilterElt)
			this._trFilterElt.value = linkTraitFilter
		return super.initFromInsAction(xml);
	}

	buildCrit(): ISearchCrit | undefined {
		// CritÃ©rion
		const subExp = new SearchAnd();
		const crit = new SearchOccurencesSubExp(this._opElt.value as EOperatorsSimple, Number.parseInt(this._opValueElt.value), subExp);

		// Condition sur le subExp
		let searchItemLinks: SearchItemLinks;
		switch (this.critArea) {
		case UiCritSubRequest.AREA_DESC :
			searchItemLinks = new SearchItemLinks("LinkDesc");
			break;
		case UiCritSubRequest.AREA_ASC :
			searchItemLinks = new SearchItemLinks("LinkAsc");
			break;
		case UiCritSubRequest.AREA_PARENTS :
			searchItemLinks = new SearchItemLinks("LinkParents");
			break;
		default :
			// AREA_CHILDREN
			searchItemLinks = new SearchItemLinks("LinkChildren");
		}
		searchItemLinks.setPath(".");
		searchItemLinks.setLinkTraitFilter(this._trFilterElt?.value)
		subExp.push(searchItemLinks);

		// CritÃ©rions fils
		LANG.pushIfDefined(subExp, this._subExpElt.buildCrit());

		return crit;
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		crit.setAttribute("uiCrit", "wsp-uicrit-subrequest:" + this.critArea.getId());
		const subExpCritElt = crit.firstElementChild;
		// Supp du bloc de critÃ¨res "fils" non "UI"
		subExpCritElt.removeChild(subExpCritElt.lastChild);
		// Ajout du bloc de critÃ¨res "fils" "UI"
		subExpCritElt.appendChild(this._subExpElt.buildUiXmlCrit());
		return crit;
	}
}

customElements.define("wsp-uicrit-subrequest", UiCritSubRequest);
